import data from "./index";

export const buffs = [
  {
    src: "Constellation 2",
    desc: () => <>{data.constellation[1].buff}.</>,
    isGranted: (char) => char.constellation >= 2,
    affect: "self",
    addBnes: ({ bonuses }) => {
      bonuses.ES.DMG += 15;
    }
  },
  {
    src: "Constellation 6",
    desc: () => (
      <>
        {data.constellation[5].lines[0]}
        {data.constellation[5].lines[2]}
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    affect: "party",
    addBnes: ({ ATTRs }) => {
      ATTRs["Elemental Mastery"] += 200;
    }
  }
];
