import { LGreen, Electro } from "../../../../styledCpns/DataDisplay";
import { SwordDesc, TravelerNCPAs } from "../../config";

const actvTalents = [
  {
    type: "Normal Attack",
    name: "Foreign Thundershock",
    image: "3/3e/Talent_Foreign_Thundershock",
    desc: SwordDesc,
    stats: TravelerNCPAs
  },
  {
    type: "Elemental Skill",
    name: "Lightning Blade",
    image: "0/03/Talent_Lightning_Blade",
    desc: [
      {
        content: (
          <>
            Unleashes three swift thunder shadows that deal{" "}
            <Electro>Electro DMG</Electro> to opponents and leave an Abundance
            Amulet behind after hitting an opponent.
            <br />2 Abundance Amulets can be created initially. Using this skill
            will reset any Abundance Amulets that were generated.
          </>
        )
      },
      {
        heading: "Abundance Amulets",
        get content() {
          return (
            <>
              When a character is near an Abundance Amulet, they will absorb it
              and obtain the following effects:
              <br /> Restores Elemental Energy.
              <br /> {this.buff}
            </>
          );
        },
        buff: (
          <>
            Increases <LGreen>Energy Recharge</LGreen> during the Abundance
            Amulet's duration.
          </>
        )
      }
    ],
    getXtraLv: (char) => (char.constellation >= 5 ? 3 : 0),
    stats: [
      {
        name: "Skill DMG",
        dmgTypes: ["ES", "Elemental"],
        baseMult: 78.66,
        multType: 2
      }
    ],
    otherStats: (lv) => [
      {
        name: "Energy Regeneration	",
        value: Math.min(2.5 + Math.ceil(lv / 3) * 0.5, 4) + " per Amulet"
      },
      {
        name: "Energy Recharge Increase",
        value: "20%"
      },
      {
        name: "Duration",
        value: "6s"
      },
      {
        name: "Abundance Amulet Duration",
        value: "15s"
      },
      {
        name: "CD",
        value: "13.5s"
      }
    ]
  },
  {
    type: "Elemental Burst",
    name: "Bellowing Thunder",
    image: "a/a7/Talent_Bellowing_Thunder",
    desc: [
      {
        content: (
          <>
            You call upon the protection of lightning, knocking nearby opponents
            back and dealing <Electro>Electro DMG</Electro> to them.
          </>
        )
      },
      {
        heading: "Lightning Shroud",
        content: (
          <>
            When your active character's Normal or Charged Attacks hit
            opponents, they will call Falling Thunder forth, dealing{" "}
            <Electro>Electro DMG</Electro>.
            <br />
            When Falling Thunder hits opponents, it will regenerate Energy for
            that character.
            <br />
            One instance of Falling Thunder can be generated every 0.5s.
          </>
        )
      }
    ],
    getXtraLv: (char) => (char.constellation >= 3 ? 3 : 0),
    stats: [
      {
        name: "Skill DMG",
        dmgTypes: ["EB", "Elemental"],
        baseMult: 114.4,
        multType: 2
      },
      {
        name: "Falling Thunder",
        dmgTypes: ["EB", "Elemental"],
        baseMult: 32.8,
        multType: 2
      }
    ],
    otherStats: (lv) => [
      {
        name: "Energy Regeneration",
        value: Math.min((7 + Math.ceil(lv / 3) * 1) / 10, 1)
      },
      {
        name: "Duration",
        value: "12s"
      },
      {
        name: "CD",
        value: "20s"
      }
    ],
    energyCost: 80
  }
];

export default actvTalents;
