import data from "./index";

export const buffs = [
  {
    src: "Ascension 4 Passive Talent",
    desc: () => data.pasvTalents[1].buff,
    isGranted: (char) => char.ascension >= 4,
    affect: "party",
    addBnes: ({ ATTRs }) => {
      ATTRs["ATK%"] += 10;
    }
  },
  {
    src: "Constellation 6",
    desc: () => data.constellation[5].desc,
    isGranted: (char) => char.constellation >= 6,
    affect: "party",
    addBnes: ({ ATTRs }) => {
      ATTRs["Pyro DMG Bonus"] += 15;
    }
  }
];

export const debuffs = [
  {
    src: "Constellation 1",
    desc: () => data.constellation[0].desc,
    isGranted: (char) => char.constellation >= 1,
    addPntes: ({ rdMult }) => {
      rdMult["Pyro RES"] -= 15;
    }
  }
];
