import { LGreen, Geo } from "../../../../styledCpns/DataDisplay";
import { ClaymoreNaDesc, SwordPaDesc } from "../../config";
const actvTalents = [
  {
    type: "Normal Attack",
    name: "Fight Club Legend",
    image: "8/80/Talent_Fight_Club_Legend",
    desc: [
      {
        heading: "NA",
        content: (
          <>
            {ClaymoreNaDesc(4)}
            <br />
            When the 2nd and 4th strikes hit opponents, Itto will gain 1 or 2
            stacks of Superlative Superstrength, respectively.
            <br />
            Max 5 stacks. Triggering this effect will refresh the current
            duration of any existing stacks.
          </>
        )
      },
      {
        content: (
          <>
            Additionally, Itto's Normal Attack combo does not immediately reset
            after sprinting or using his Elemental Skill, "Masatsu Zetsugi:
            Akaushi Burst!"
          </>
        )
      },
      {
        heading: "CA",
        content: (
          <>
            When holding to perform a Charged Attack, Itto unleashes a series of
            Arataki Kesagiri slashes without consuming Stamina. Instead, each
            Arataki Kesagiri slash consumes 1 stack of Superlative
            Superstrength. When the final stack is consumed, Itto delivers a
            powerful final slash.
            <br />
            If no stacks of Superlative Superstrength are available, Itto will
            perform a single Saichimonji Slash.
          </>
        )
      },
      {
        heading: "PA",
        content: SwordPaDesc
      }
    ],
    stats: [
      {
        name: "1-Hit",
        dmgTypes: ["NA", "Physical"],
        baseMult: 79.23,
        multType: 1
      },
      {
        name: "2-Hit",
        dmgTypes: ["NA", "Physical"],
        baseMult: 76.37,
        multType: 1
      },
      {
        name: "3-Hit",
        dmgTypes: ["NA", "Physical"],
        baseMult: 91.64,
        multType: 1
      },
      {
        name: "4-Hit",
        dmgTypes: ["NA", "Physical"],
        baseMult: 117.22,
        multType: 1
      },
      {
        name: "Arataki Kesagiri Combo Slash DMG",
        dmgTypes: ["CA", "Physical"],
        baseMult: 91.16,
        multType: 1,
        getTlBnes: ({ char, selfMCs, ATTRs }) => ({
          xtraFlat:
            char.ascension >= 4 && selfMCs.BCs[1].activated
              ? Math.round(ATTRs.DEF * 0.35)
              : 0
        })
      },
      {
        name: "Arataki Kesagiri Final Slash DMG",
        dmgTypes: ["CA", "Physical"],
        baseMult: 190.92,
        multType: 1,
        getTlBnes: ({ char, selfMCs, ATTRs }) => ({
          xtraFlat:
            char.ascension >= 4 && selfMCs.BCs[1].activated
              ? Math.round(ATTRs.DEF * 0.35)
              : 0
        })
      },
      {
        name: "Superlative Superstrength Duration",
        noCalc: true,
        getValue: () => "60s"
      },

      {
        name: "Saichimonji Slash DMG",
        dmgTypes: ["CA", "Physical"],
        baseMult: 90.47,
        multType: 1
      },
      {
        name: "Saichimonji Slash Stamina Cost",
        noCalc: true,
        getValue: () => 20
      },
      {
        name: "Plunge DMG",
        dmgTypes: ["PA", "Physical"],
        baseMult: 81.83,
        multType: 1
      },
      {
        name: "Low Plunge",
        dmgTypes: ["PA", "Physical"],
        baseMult: 163.63,
        multType: 1
      },
      {
        name: "High Plunge",
        dmgTypes: ["PA", "Physical"],
        baseMult: 204.39,
        multType: 1
      }
    ]
  },
  {
    type: "Elemental Skill",
    name: "Masatsu Zetsugi: Akaushi Burst!",
    image: "5/51/Talent_Masatsu_Zetsugi_Akaushi_Burst%21",
    desc: [
      {
        content: (
          <>
            Hurls Ushi, the young akaushi bull and auxiliary member of the
            Arataki Gang, dealing <Geo>Geo DMG</Geo> to opponents on hit. When
            Ushi hits opponents, Arataki Itto gains 1 stack of Superlative
            Superstrength.
          </>
        )
      },
      {
        content: (
          <>
            Ushi will remain on the field and provide support in the following
            ways:
            <br /> Taunts surrounding opponents and draws their attacks.
            <br /> Inherits HP based on a percentage of Arataki Itto's Max HP.
            <br /> When Ushi takes DMG, Arataki Itto gains 1 stack of
            Superlative Superstrength. Only 1 stack can be gained in this way
            every 2s.
            <br /> Ushi will flee when its HP reaches 0 or its duration ends.
            It will grant Arataki Itto 1 stack of Superlative Superstrength when
            it leaves.
          </>
        )
      },
      {
        heading: "Hold",
        content: <>Adjust throwing angle.</>
      },
      {
        content: (
          <>
            Ushi is considered a Geo construct. Arataki Itto can only deploy 1
            Ushi on the field at any one time.
          </>
        )
      }
    ],
    getXtraLv: (char) => (char.constellation >= 3 ? 3 : 0),
    stats: [
      {
        name: "Skill DMG",
        dmgTypes: ["ES", "Elemental"],
        baseMult: 307.2,
        multType: 2
      }
    ],
    otherStats: () => [
      {
        name: "Inherited HP",
        value: "100%"
      },
      {
        name: "Duration",
        value: "6s"
      },
      {
        name: "CD",
        value: "10s"
      }
    ]
  },
  {
    type: "Elemental Burst",
    name: "Royal Descent: Behold, Itto the Evil!",
    image: "5/50/Talent_Royal_Descent_Behold%2C_Itto_the_Evil%21",
    desc: [
      {
        get content() {
          return (
            <>
              Time to show 'em the might of the Arataki Gang! For a time, Itto
              lets out his inner Raging Oni King, wielding his Oni King's
              Kanabou in battle.
              <br />
              This state has the following special properties:
              <br />
              {this.lines[0]}
              {this.lines[1]}
              <br /> On hit, the 1st and 3rd strikes of his attack combo will
              each grant Arataki Itto 1 stack of Superlative Superstrength.
              <br /> Decreases Itto's Elemental and Physical RES by 20%.
            </>
          );
        },
        lines: [
          <>
             <LGreen>Converts</LGreen> Itto's Normal, Charged, and Plunging
            Attacks to <Geo>Geo DMG</Geo>. This cannot be overridden.
          </>,
          <>
            <br /> Increases Itto's <LGreen>Normal Attack SPD</LGreen>. Also
            increases his <LGreen>ATK</LGreen> based on his <LGreen>DEF</LGreen>
            .
          </>
        ]
      },
      {
        content: (
          <>
            The Raging Oni King state will be cleared when Itto leaves the
            field.
          </>
        )
      }
    ],
    getXtraLv: (char) => (char.constellation >= 5 ? 3 : 0),
    stats: [
      {
        name: "ATK Bonus",
        baseSType: "DEF",
        baseMult: 57.6,
        multType: 2
      }
    ],
    otherStats: () => [
      {
        name: "ATK SPD Bonus",
        value: "10%"
      },
      {
        name: "Duration",
        value: "11s"
      },
      {
        name: "CD",
        value: "18s"
      }
    ],
    energyCost: 70
  }
];

export default actvTalents;
