import { LGreen, Green, Red } from "../../../../styledCpns/DataDisplay";
import { getXtraTalentLv } from "../../../../calculators/helpers";
import data from "./index";

export const buffs = [
  {
    src: "Elemental Burst",
    desc: ({ toSelf, char, party, inputs }) => (
      <>
        Omen increases{" "}
        <Green>{buffs[0].bnValue(toSelf, char, party, inputs)}%</Green>{" "}
        <LGreen>DMG taken</LGreen> by opponents. (This is actually a DMG bonus.)
      </>
    ),
    isGranted: () => true,
    affect: "party",
    labels: ["Elemental Burst Level"],
    inputs: [1],
    inputTypes: ["text"],
    maxs: [13],
    addBnes: ({ bonuses, toSelf, char, party, inputs }) => {
      bonuses.AllDmg += buffs[0].bnValue(toSelf, char, party, inputs);
    },
    bnValue: (toSelf, char, party, inputs) => {
      const level = toSelf
        ? char["Elemental Burst"] +
          getXtraTalentLv(data.actvTalents[2], char, party)
        : inputs[0];
      return Math.min(40 + level * 2, 60);
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: ({ ATTRs }) => (
      <>
        {data.pasvTalents[1].desc}{" "}
        <Red>Hydro DMG Bonus: {buffs[1].bnValue(ATTRs)}%.</Red>
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["Hydro DMG Bonus"] += buffs[1].bnValue(ATTRs);
    },
    bnValue: (ATTRs) => Math.round(ATTRs["Energy Recharge"] * 2) / 10
  },
  {
    src: "Constellation 1",
    desc: () => data.constellation[0].desc,
    isGranted: (char) => char.constellation >= 1,
    affect: "party",
    addBnes: ({ rxnBnes }) => {
      rxnBnes["Electro-Charged"] += 15;
      rxnBnes.Swirl += 15;
      rxnBnes.Vaporize += 15;
    }
  },
  {
    src: "Constellation 4",
    desc: () => data.constellation[3].desc,
    isGranted: (char) => char.constellation >= 4,
    affect: "party",
    addBnes: ({ ATTRs }) => {
      ATTRs["CRIT Rate"] += 15;
    }
  },
  {
    src: "Constellation 6",
    desc: () => data.constellation[5].desc,
    isGranted: (char) => char.constellation >= 6,
    affect: "self",
    selfLabels: ["Stacks"],
    inputs: [1],
    inputTypes: ["select"],
    maxs: [3],
    addBnes: ({ bonuses, inputs }) => {
      bonuses.CA.DMG += 60 * inputs[0];
    }
  }
];
