import { Red } from "../../../../styledCpns/DataDisplay";
import data from "./index";

export const buffs = [
  {
    src: "Ascension 1 Passive Talent",
    desc: () => data.pasvTalents[0].desc,
    affect: "self",
    isGranted: (char) => char.ascension >= 1
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => data.pasvTalents[1].desc,
    affect: "party",
    isGranted: (char) => char.ascension >= 4,
    addBnes: ({ ATTRs }) => {
      ATTRs["Elemental Mastery"] += 125;
    }
  },
  {
    src: "Constellation 2",
    desc: ({ ATTRs, charBCs }) => (
      <>
        {data.constellation[1].buff}{" "}
        <Red>DMG Bonus (base): {buffs[2].bnValue(ATTRs, charBCs)}.</Red>
      </>
    ),
    isGranted: (char) => char.constellation >= 2,
    affect: "self",
    selfLabels: ["Stacks"],
    inputs: [1],
    inputTypes: ["select"],
    maxs: [4],
    addFinalBnes: ({ ATTRs, bonuses, charBCs }) => {
      bonuses.ES.Flat += buffs[2].bnValue(ATTRs, charBCs);
    },
    bnValue: (ATTRs, charBCs) =>
      Math.round(ATTRs.DEF * 0.3 * charBCs[2].inputs[0])
  },
  {
    src: "Constellation 4",
    desc: () => data.constellation[3].desc,
    affect: "party",
    isGranted: (char) => char.constellation >= 4,
    addBnes: ({ bonuses }) => {
      bonuses.PA.DMG += 30;
    }
  },
  {
    src: "Constellation 6",
    desc: () => data.constellation[5].desc,
    affect: "party",
    isGranted: (char) => char.constellation >= 6,
    addBnes: ({ bonuses }) => {
      bonuses.AllDmg += 17;
    }
  }
];
