import { Red } from "../../../../styledCpns/DataDisplay";
import data from "./index";

export const buffs = [
  {
    src: "Ascension 1 Passive Talent",
    desc: () => data.pasvTalents[0].desc,
    isGranted: (char) => char.ascension >= 1,
    affect: "party",
    selfLabels: ["Stacks"],
    labels: ["Stacks"],
    inputs: [1],
    inputTypes: ["select"],
    maxs: [5],
    addBnes: ({ ATTRs, inputs }) => {
      ATTRs["Shield Strength"] += 5 * inputs[0];
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: ({ ATTRs }) => (
      <>
        {data.pasvTalents[1].desc}{" "}
        <Red>DMG Bonus (base): {buffs[1].bnValue(ATTRs)}.</Red>
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "self",
    bnValue: (ATTRs) => Math.round(ATTRs.HP * 0.022)
  },
  {
    src: "Constellation 6",
    desc: () => data.constellation[5].desc,
    isGranted: (char) => char.constellation >= 6,
    affect: "party",
    addBnes: ({ bonuses }) => {
      bonuses.NA.DMG += 15;
      bonuses.CA.DMG += 15;
      bonuses.PA.DMG += 15;
    }
  }
];
