import { Red } from "../../../../styledCpns/DataDisplay";
import { elmts } from "../../../elements";
import data from "./index";

export const buffs = [
  {
    src: "Ascension 1 Passive Talent",
    desc: () => data.pasvTalents[0].desc,
    isGranted: (char) => char.ascension >= 1,
    affect: "party",
    selfLabels: ["Stacks"],
    labels: ["Stacks"],
    inputs: [1],
    inputTypes: ["select"],
    maxs: [5],
    addBnes: ({ ATTRs, inputs }) => {
      ATTRs["Shield Strength"] += inputs[0] * 5;
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: ({ ATTRs }) => {
      const result = buffs[1].bnValue(ATTRs);
      const Bonus = (val) => <Red>DMG Bonus (base): {val}.</Red>;
      return (
        <>
          {data.pasvTalents[1].lines[0]} {Bonus(result[0])}
          {data.pasvTalents[1].lines[1]} {Bonus(result[1])}
          {data.pasvTalents[1].lines[2]} {Bonus(result[2])}
        </>
      );
    },
    isGranted: (char) => char.ascension >= 4,
    affect: "self",
    addFinalBnes: ({ ATTRs, bonuses }) => {
      const result = buffs[1].bnValue(ATTRs);
      bonuses.NA.Flat += result[0];
      bonuses.CA.Flat += result[0];
      bonuses.PA.Flat += result[0];
      bonuses.ES.Flat += result[1];
      bonuses.EB.Flat += result[2];
    },
    bnValue: (ATTRs) =>
      [1.39, 1.9, 33].map((mult) => Math.round((ATTRs.HP * mult) / 100))
  }
];

export const debuffs = [
  {
    src: "Jade Shield",
    desc: () => data.actvTalents[1].desc[3].debuff,
    isGranted: () => true,
    addPntes: ({ rdMult }) => {
      for (const res of ["Physical", ...elmts]) {
        rdMult[res + " RES"] -= 20;
      }
    }
  }
];
