import data from "./index";

export const buffs = [
  {
    src: "Alternate Sprint",
    desc: () => (
      <>
        {data.actvTalents[3].desc[1].lines[1]}
        {data.actvTalents[3].desc[1].lines[3]}
      </>
    ),
    isGranted: () => true,
    affect: "self",
    canInfuse: () => true,
    infuseRange: ["NA", "CA", "PA"],
    canBeOverrided: true
  },
  {
    src: "Ascension 1 Passive Talent",
    desc: () => data.pasvTalents[0].desc,
    isGranted: (char) => char.ascension >= 1,
    affect: "self",
    addBnes: ({ bonuses }) => {
      bonuses.NA.DMG += 30;
      bonuses.CA.DMG += 30;
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => (
      <>
        {data.pasvTalents[1].lines[0]}
        {data.pasvTalents[1].lines[2]}
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["Cryo DMG Bonus"] += 18;
    }
  },
  {
    src: "Constellation 6",
    desc: () => data.constellation[5].buff,
    isGranted: (char) => char.constellation >= 6,
    affect: "self",
    addBnes: ({ bonuses }) => {
      bonuses.CA.DMG += 298;
    }
  }
];

export const debuffs = [
  {
    src: "Constellation 4",
    desc: () => data.constellation[3].desc,
    isGranted: (char) => char.constellation >= 4,
    addPntes: ({ rdMult }) => {
      rdMult.DEF += 30;
    }
  }
];
