import { LGreen, Cryo } from "../../../../styledCpns/DataDisplay";
import {
  BowNaDesc,
  BowCaDesc,
  BowPaDesc,
  lightPAs,
  bowCAs,
  TalentLvMults
} from "../../config";

const actvTalents = [
  {
    type: "Normal Attack",
    name: "Rapid Fire",
    image: "5/50/Talent_Rapid_Fire",
    desc: [
      { heading: "NA", content: BowNaDesc(4) },
      {
        heading: "CA",
        content: BowCaDesc(
          "biting frost",
          "A fully charged frost arrow",
          "Cryo"
        )
      },
      { heading: "PA", content: BowPaDesc }
    ],
    stats: [
      {
        name: "1-Hit",
        dmgTypes: ["NA", "Physical"],
        baseMult: [21.12, 23.76],
        multType: 1
      },
      {
        name: "2-Hit",
        dmgTypes: ["NA", "Physical"],
        baseMult: 43.12,
        multType: 1
      },
      {
        name: "3-Hit",
        dmgTypes: ["NA", "Physical"],
        baseMult: 52.8,
        multType: 1
      },
      {
        name: "4-Hit",
        dmgTypes: ["NA", "Physical"],
        baseMult: 65.65,
        multType: 1
      },
      ...bowCAs,
      ...lightPAs("bow")
    ]
  },
  {
    type: "Elemental Skill",
    name: "Frozen Wilds",
    image: "9/9a/Talent_Frozen_Wilds",
    desc: [
      {
        content: (
          <>
            Aloy throws a Freeze Bomb in the targeted direction that explodes on
            impact, dealing <Cryo>Cryo DMG</Cryo>. After it explodes, the Freeze
            Bomb will split up into many Chillwater Bomblets that explode on
            contact with opponents or after a short delay, dealing{" "}
            <Cryo>Cryo DMG</Cryo>.
            <br />
            When a Freeze Bomb or Chillwater Bomblet hits an opponent, the
            opponent's ATK is decreased and Aloy receives 1 Coil stack.
            <br />
            Aloy can gain up to 1 Coil stack every 0.1s.
          </>
        )
      },
      {
        heading: "Coil",
        get content() {
          return (
            <>
               {this.lines[0]}
              <br /> {this.lines[1]}
            </>
          );
        },
        lines: [
          <>
            Each stack increases Aloy's <LGreen>Normal Attack DMG</LGreen>.
          </>,
          <>
            When Aloy has 4 Coil stacks, all stacks of Coil are cleared. She
            then enters the Rushing Ice state, which further increases the{" "}
            <LGreen>DMG</LGreen> dealt by her <LGreen>Normal Attacks</LGreen>{" "}
            and converts her <LGreen>Normal Attack DMG</LGreen> to{" "}
            <Cryo>Cryo DMG</Cryo>.
          </>
        ]
      },
      {
        content: (
          <>
            While in the Rushing Ice state, Aloy cannot obtain new Coil stacks.
            Coil effects will be cleared 30s after Aloy leaves the field.
          </>
        )
      }
    ],
    stats: [
      {
        name: "Freeze Bomb",
        dmgTypes: ["ES", "Elemental"],
        baseMult: 177.6,
        multType: 2
      },
      {
        name: "Chillwater Bomblets",
        dmgTypes: ["ES", "Elemental"],
        baseMult: 40,
        multType: 2
      }
    ],
    otherStats: (lv) => [
      {
        name: "ATK Decrease",
        value: Math.min(11 + Math.ceil(lv / 3), 15) + "%"
      },
      {
        name: "ATK Decrease Duration",
        value: "6s"
      },
      {
        name: "Coil Normal Attack DMG Bonus",
        value: [1, 2, 3]
          .map(
            (stack) =>
              Math.round(584.6 * TalentLvMults[5][lv] * stack) / 100 + "%"
          )
          .join(" / ")
      },
      {
        name: "Rushing Ice Normal Attack DMG Bonus",
        value: Math.round(2923 * TalentLvMults[5][lv]) / 100 + "%"
      },
      {
        name: "Rushing Ice Duration",
        value: "10s"
      },
      {
        name: "CD",
        value: "20s"
      }
    ]
  },
  {
    type: "Elemental Burst",
    name: "Prophecies of Dawn",
    image: "b/b4/Talent_Prophecies_of_Dawn",
    desc: [
      {
        content: (
          <>
            Aloy throws a Power Cell filled with Cryo in the targeted direction,
            then detonates it with an arrow, dealing AoE <Cryo>Cryo DMG</Cryo>.
          </>
        )
      }
    ],
    stats: [
      {
        name: "Skill DMG",
        dmgTypes: ["EB", "Elemental"],
        baseMult: 359.2,
        multType: 2
      }
    ],
    otherStats: () => [
      {
        name: "CD",
        value: "12s"
      }
    ],
    energyCost: 40
  }
];

export default actvTalents;
