import data from "./index";

export const buffs = [
  {
    src: "Ascension 4 Passive Talent",
    desc: () => data.pasvTalents[1].desc,
    isGranted: (char) => char.ascension >= 4,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["Hydro DMG Bonus"] += 20;
    }
  },
  {
    src: "Constellation 4",
    desc: () => data.constellation[3].desc,
    isGranted: (char) => char.constellation >= 4,
    affect: "self",
    addBnes: ({ bonuses }) => {
      bonuses.ES.mult += 50;
    }
  }
];

export const debuffs = [
  {
    src: "Constellation 2",
    desc: () => data.constellation[1].debuff,
    isGranted: (char) => char.constellation >= 2,
    addPntes: ({ rdMult }) => {
      rdMult["Hydro RES"] -= 15;
    }
  }
];
