import { LGreen, Green, Geo } from "../../../../styledCpns/DataDisplay";
import {
  BowNaDesc,
  BowCaDesc,
  BowPaDesc,
  lightPAs,
  bowCAs
} from "../../config";

const actvTalents = [
  {
    type: "Normal Attack",
    name: "Ripping Fang Fletching",
    image: "b/b5/Talent_Ripping_Fang_Fletching",
    desc: [
      { heading: "NA", content: BowNaDesc(4) },
      {
        heading: "CA",
        content: BowCaDesc(
          "stone crystals",
          "A fully charged crystalline arrow",
          "Geo"
        )
      },
      { heading: "PA", content: BowPaDesc }
    ],
    stats: [
      {
        name: "1-Hit",
        dmgTypes: ["NA", "Physical"],
        baseMult: 37.75,
        multType: 1
      },
      {
        name: "2-Hit",
        dmgTypes: ["NA", "Physical"],
        baseMult: 37.15,
        multType: 1
      },
      {
        name: "3-Hit",
        dmgTypes: ["NA", "Physical"],
        baseMult: 49.45,
        multType: 1
      },
      {
        name: "4-Hit",
        dmgTypes: ["NA", "Physical"],
        baseMult: 59,
        multType: 1
      },
      ...bowCAs,
      ...lightPAs("bow")
    ]
  },
  {
    type: "Elemental Skill",
    name: "Inuzaka All-Round Defense",
    image: "e/e6/Talent_Inuzaka_All-Round_Defense",
    desc: [
      {
        content: (
          <>
            Deals AoE <Geo>Geo DMG</Geo> and sets up a General's War Banner.
          </>
        )
      },
      {
        heading: "General's War Banner",
        get content() {
          return (
            <>
              {this.lines[0]}
              {this.lines[1]}.{this.lines[2]}
              {this.lines[3]}
            </>
          );
        },
        lines: [
          <>
            Provides up to 3 buffs to active characters within the skill's AoE
            based on the number of <Geo>Geo</Geo> characters in the party at the
            time of casting:
          </>,
          <>
            <br /> 1 Geo character: Adds "Standing Firm" -{" "}
            <LGreen>DEF Bonus</LGreen>
          </>,
          <>
            <br /> 2 Geo characters: Adds "Impregnable" - Increased resistance
            to interruption.
          </>,
          <>
            <br /> 3 Geo character: Adds "Crunch" - <Green>15%</Green>{" "}
            <LGreen>Geo DMG Bonus</LGreen>.
          </>
        ]
      },
      {
        content: (
          <>
            Gorou can deploy only 1 General's War Banner on the field at any one
            time. Characters can only benefit from 1 General's War Banner at a
            time. When a party member leaves the field, the active buff will
            last for 2s.
          </>
        )
      },
      {
        heading: "Hold",
        content: <>Adjust the location of the skill.</>
      }
    ],
    getXtraLv: (char) => (char.constellation >= 3 ? 3 : 0),
    stats: [
      {
        name: "Skill DMG",
        dmgTypes: ["ES", "Elemental"],
        baseMult: 107.2,
        multType: 2
      },
      {
        name: "DEF Increase",
        baseMult: 0,
        multType: 2,
        baseFlat: 206,
        flatType: 2
      }
    ],
    otherStats: () => [
      {
        name: "Geo DMG Bonus",
        value: "10%"
      },
      {
        name: "Duration",
        value: "10s"
      },
      {
        name: "CD",
        value: "10s"
      }
    ]
  },
  {
    type: "Elemental Burst",
    name: "Juuga: Forward Unto Victory",
    image: "f/f9/Talent_Juuga_Forward_Unto_Victory",
    desc: [
      {
        content: (
          <>
            Displaying his valor as a general, Gorou deals AoE{" "}
            <Geo>Geo DMG</Geo> and creates a field known as General's Glory to
            embolden his comrades.
          </>
        )
      },
      {
        heading: "General's Glory",
        content: (
          <>
            This field has the following properties:
            <br /> Like the General's War Banner created by Inuzaka All-Round
            Defense, provides buffs to active characters within the skill's AoE
            based on the number of Geo characters in the party. Also moves
            together with your active character.
            <br /> Generates 1 Crystal Collapse every 1.5s that deals AoE{" "}
            <Geo>Geo DMG</Geo> to 1 opponent within the skill's AoE.
            <br /> Pulls 1 elemental shard in the skill's AoE to your active
            character's position every 1.5s (elemental shards are created by
            Crystallize reactions).
          </>
        )
      },
      {
        content: (
          <>
            If a General's War Banner created by Gorou currently exists on the
            field when this ability is used, it will be destroyed. In addition,
            for the duration of General's Glory, Gorou's Elemental Skill
            "Inuzaka All-Round Defense" will not create the General's War
            Banner.
            <br />
            If Gorou falls, the effects of General's Glory will be cleared.
          </>
        )
      }
    ],
    getXtraLv: (char) => (char.constellation >= 5 ? 3 : 0),
    stats: [
      {
        name: "Skill DMG",
        dmgTypes: ["EB", "Elemental"],
        baseSType: "DEF",
        baseMult: 98.22,
        multType: 2
      },
      {
        name: "Crystal Collapse DMG",
        dmgTypes: ["EB", "Elemental"],
        baseSType: "DEF",
        baseMult: 61.3,
        multType: 2
      }
    ],
    otherStats: () => [
      {
        name: "Duration",
        value: "9s"
      },
      {
        name: "CD",
        value: "20s"
      }
    ],
    energyCost: 80
  }
];

export default actvTalents;
