import { Red } from "../../../../styledCpns/DataDisplay";
import { TalentLvMults } from "../../config";
import characters from "../../index";
import { incElementalBnes } from "../../../../helpers/calculators";
import { getXtraTalentLv } from "../../../../calculators/helpers";
import data from "./index";

function countGeo(char, party) {
  let result = characters[char.name].vision === "Geo" ? 1 : 0;
  for (let { name } of party) {
    if (name && characters[name].vision === "Geo") result++;
  }
  return result;
}

export const buffs = [
  {
    src: "Elemental Skill",
    desc: ({ toSelf, char, inputs, party }) => {
      const numOfGeo = countGeo(char, party);
      return (
        <>
          {data.actvTalents[1].desc[1].lines[0]}
          <span className={numOfGeo >= 1 ? "" : "unavailable"}>
            {data.actvTalents[1].desc[1].lines[1]}
            {toSelf ? (
              "."
            ) : (
              <>
                : <Red>{buffs[0].bnValue(inputs[0])}.</Red>
              </>
            )}
          </span>
          <span className={numOfGeo >= 2 ? "" : "unavailable"}>
            {data.actvTalents[1].desc[1].lines[2]}
          </span>
          <span className={numOfGeo >= 3 ? "" : "unavailable"}>
            {data.actvTalents[1].desc[1].lines[3]}
          </span>
        </>
      );
    },
    isGranted: () => true,
    affect: "party",
    labels: ["Elemental Skill Level"],
    inputs: [1],
    inputTypes: ["text"],
    maxs: [13],
    addBnes: ({ toSelf, ATTRs, char, inputs, party }) => {
      const { bnValue } = buffs[0];
      const level =
        char["Elemental Skill"] +
        getXtraTalentLv(data.actvTalents[1], char, party);
      ATTRs.DEF += bnValue(toSelf ? level : inputs[0]);
      if (countGeo(char, party) > 2) {
        ATTRs["Geo DMG Bonus"] += 15;
      }
    },
    bnValue: (level) => Math.round(206 * TalentLvMults[2][level])
  },
  {
    src: "Ascension 1 Pasive Talent",
    desc: () => data.pasvTalents[0].desc,
    isGranted: (char) => char.ascension >= 1,
    affect: "party",
    addBnes: ({ ATTRs }) => {
      ATTRs["DEF%"] += 25;
    }
  },
  {
    src: "Ascension 4 Pasive Talent",
    desc: () => data.pasvTalents[1].desc,
    isGranted: (char) => char.ascension >= 4,
    affect: "self",
    addFinalBnes: ({ bonuses, ATTRs }) => {
      bonuses.ES.Flat += Math.round(ATTRs.DEF * 1.56);
      bonuses.EB.Flat += Math.round(ATTRs.DEF * 0.156);
    }
  },
  {
    src: "Constellation 6",
    desc: ({ char, party }) => {
      const numOfGeo = countGeo(char, party);
      return (
        <>
          {data.constellation[5].lines[0]}
          <span className={numOfGeo === 1 ? "" : "unavailable"}>
            {data.constellation[5].lines[1]}
          </span>
          <span className={numOfGeo === 2 ? "" : "unavailable"}>
            {data.constellation[5].lines[2]}
          </span>
          <span className={numOfGeo >= 3 ? "" : "unavailable"}>
            {data.constellation[5].lines[3]}
          </span>
        </>
      );
    },
    isGranted: (char) => char.constellation >= 6,
    affect: "party",
    addBnes: ({ bonuses, char, infusion, party }) => {
      const value = [0, 0.1, 0.2, 0.4, 0.4][countGeo(char, party)];
      incElementalBnes(char.name, infusion, "Geo", bonuses, "CD", value);
    }
  }
];
