import { LGreen, Green, Red } from "../../../../styledCpns/DataDisplay";
import { TalentLvMults } from "../../config";
import { getXtraTalentLv } from "../../../../calculators/helpers";
import data from "./index";

export const buffs = [
  {
    src: "Elemental Skill",
    desc: () => (
      <>
        {data.actvTalents[1].desc[1].lines[0]}, and convert her{" "}
        {data.actvTalents[1].desc[1].lines[1]}.
      </>
    ),
    isGranted: () => true,
    affect: "self",
    addFinalBnes: ({ ATTRs, char, party }) => {
      const level =
        char["Elemental Skill"] +
        getXtraTalentLv(data.actvTalents[1], char, party);
      ATTRs.ATK += Math.round(
        (ATTRs.HP * 3.84 * TalentLvMults[4][level]) / 100
      );
    },
    canInfuse: () => true,
    infuseRange: ["NA", "CA", "PA"],
    canBeOverrided: false
  },
  {
    src: "Ascension 1 Passive Talent",
    desc: () => data.pasvTalents[0].desc,
    isGranted: (char) => char.ascension >= 1,
    affect: "teammates",
    addBnes: ({ ATTRs }) => {
      ATTRs["CRIT Rate"] += 12;
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => data.pasvTalents[1].desc,
    isGranted: (char) => char.ascension >= 4,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["Pyro DMG Bonus"] += 33;
    }
  },
  {
    src: "Constellation 2",
    desc: ({ ATTRs }) => (
      <>
        {data.constellation[1].buff}.{" "}
        <Red>DMG Bonus (base): {buffs[3].bnValue(ATTRs)}.</Red>
      </>
    ),
    isGranted: (char) => char.constellation >= 2,
    affect: "self",
    addFinalBnes: ({ ATTRs, bonuses }) => {
      bonuses.ES.Flat += buffs[3].bnValue(ATTRs);
    },
    bnValue: (ATTRs) => Math.round(ATTRs.HP / 10)
  },
  {
    src: "Constellation 6",
    desc: () => (
      <>
        When Hu Tao's HP drops below 25%, or when she suffers a lethal strike,
        her <LGreen>CRIT Rate</LGreen> is increased by <Green>100%</Green> for
        10s.
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["CRIT Rate"] += 100;
    }
  }
];
