import { Pyro } from "../../../../styledCpns/DataDisplay";
import { PolearmDesc, CaStamina, mediumPAs } from "../../config";
import { buffs } from "./modifiers";

const actvTalents = [
  {
    type: "Normal Attack",
    name: "Swiftshatter Spear",
    image: "c/ca/Talent_Swiftshatter_Spear",
    desc: PolearmDesc(4),
    stats: [
      {
        name: "1-Hit",
        dmgTypes: ["NA", "Physical"],
        baseMult: 44.39,
        multType: 4
      },
      {
        name: "2-Hit",
        dmgTypes: ["NA", "Physical"],
        baseMult: 43.63,
        multType: 4
      },
      {
        name: "3-Hit (x2)",
        dmgTypes: ["NA", "Physical"],
        baseMult: 26.79,
        multType: 4
      },
      {
        name: "4-Hit",
        dmgTypes: ["NA", "Physical"],
        baseMult: 67.36,
        multType: 4
      },
      {
        name: "Charged Attack",
        dmgTypes: ["CA", "Physical"],
        baseMult: 112.75,
        multType: 4
      },
      CaStamina(25),
      ...mediumPAs
    ]
  },
  {
    type: "Elemental Skill",
    name: "Blazing Blessing",
    image: "9/9b/Talent_Blazing_Blessing",
    desc: [
      {
        get content() {
          return (
            <>
              Thoma vaults forward with his polearm and delivers a flame-filled
              flying kick that deals AoE <Pyro>Pyro DMG</Pyro>, while also
              summoning a defensive Blazing Barrier. At the moment of casting,
              Thoma's Elemental Skill applies <Pyro>Pyro</Pyro> to himself.
              <br />
              The DMG Absorption of the Blazing Barrier scales off Thoma's Max
              HP.
              <br />
              The Blazing Barrier has the following traits:
              {this.shield}
            </>
          );
        },
        shield: [
          <>
            <br /> Absorbs Pyro DMG 250% more effectively.
            <br /> When a new Blazing Barrier is obtained, the remaining DMG
            Absorption of an existing Blazing Barrier will stack and its
            duration will be refreshed.
          </>
        ]
      },
      {
        content: (
          <>
            The maximum DMG Absorption of the Blazing Barrier will not exceed a
            certain percentage of Thoma's Max HP.
          </>
        )
      }
    ],
    getXtraLv: (char) => (char.constellation >= 3 ? 3 : 0),
    stats: [
      {
        name: "Skill DMG",
        dmgTypes: ["ES", "Elemental"],
        baseMult: 146.4,
        multType: 2
      },
      {
        name: "Shield DMG Absorption",
        baseSType: "HP",
        baseMult: 7.2,
        multType: 2,
        baseFlat: 693,
        flatType: 3
      },
      { name: "Shield Duration", noCalc: true, getValue: () => "8s" },
      {
        name: "Max Shield DMG Absorption",
        baseSType: "HP",
        baseMult: 19.6,
        multType: 2,
        baseFlat: 1887,
        flatType: 3
      }
    ],
    otherStats: () => [
      {
        name: "CD",
        value: "15s"
      }
    ]
  },
  {
    type: "Elemental Burst",
    name: "Crimson Ooyoroi",
    image: "e/e4/Talent_Crimson_Ooyoroi",
    desc: [
      {
        content: (
          <>
            Thoma spins his polearm, slicing at his foes with roaring flames
            that deal AoE <Pyro>Pyro DMG</Pyro> and weave themselves into a
            Scorching Ooyoroi.
          </>
        )
      },
      {
        heading: "Scorching Ooyoroi",
        content: (
          <>
            While Scorching Ooyoroi is in effect, the active character's Normal
            Attacks will trigger Fiery Collapse, dealing AoE{" "}
            <Pyro>Pyro DMG</Pyro> and summoning a Blazing Barrier.
            <br />
            Fiery Collapse can be triggered once every 1s.
          </>
        )
      },
      {
        get content() {
          return (
            <>
              Except for the amount of DMG they can absorb, the Blazing Barriers
              created in this way are identical to those created by Thoma's
              Elemental Skill, Blazing Blessing:{actvTalents[1].desc[0].shield}
            </>
          );
        }
      },
      {
        content: (
          <>
            The maximum DMG Absorption of the Blazing Barrier will not exceed a
            certain percentage of Thoma's Max HP.
          </>
        )
      },
      {
        content: (
          <>If Thoma falls, the effects of Scorching Ooyoroi will be cleared.</>
        )
      }
    ],
    getXtraLv: (char) => (char.constellation >= 5 ? 3 : 0),
    stats: [
      {
        name: "Skill DMG",
        dmgTypes: ["ES", "Elemental"],
        baseMult: 88,
        multType: 2
      },
      {
        name: "Fiery Collapse DMG",
        dmgTypes: ["ES", "Elemental"],
        baseMult: 58,
        multType: 2,
        getTlBnes: ({ char, selfMCs, ATTRs }) => ({
          xtraFlat:
            buffs[1].isGranted(char) && selfMCs.BCs[1].activated
              ? buffs[1].bnValue(ATTRs)
              : 0
        })
      },
      {
        name: "Shield DMG Absorption",
        baseSType: "HP",
        baseMult: 1.14,
        multType: 2,
        baseFlat: 110,
        flatType: 3
      }
    ],
    otherStats: () => [
      {
        name: "Shield Duration",
        value: "8s"
      },
      {
        name: "Scorching Ooyoroi Duration",
        value: "15s"
      },
      {
        name: "CD",
        value: "20s"
      }
    ],
    energyCost: 80
  }
];

export default actvTalents;
