import data from "./index";

export const buffs = [
  {
    src: "Ascension 1 Passive Talent",
    desc: () => data.pasvTalents[0].buff,
    isGranted: (char) => char.ascension >= 1,
    affect: "self",
    selfLabels: ["Stacks"],
    inputs: [1],
    inputTypes: ["select"],
    maxs: [4],
    addBnes: ({ ATTRs, inputs }) => {
      ATTRs["Pyro DMG Bonus"] += 5 * inputs[0];
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => data.pasvTalents[1].desc,
    isGranted: (char) => char.ascension >= 4,
    affect: "self"
  },
  {
    src: "Constellation 2",
    desc: () => data.constellation[1].desc,
    isGranted: (char) => char.constellation >= 2,
    affect: "self",
    addBnes: ({ bonuses }) => {
      bonuses.CA.CR += 0.2;
    }
  }
];
