import styled from "styled-components";
import { Img } from "../../styledCpns/DataDisplay";
import { Box } from "../../styledCpns/Layout";
import { colorCodes, wikia } from "../../data/misc";

export const StyledList = styled.div`
  .details,
  .overview {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    transition: 200ms linear;
  }
  .overview.close,
  .caret.left {
    right: 100%;
  }
  .overview.open {
    right: 0;
  }
  .details.close,
  .caret.right {
    left: 100%;
  }
  .details.open {
    left: 0;
  }
  .caret {
    position: absolute;
    top: 0;
    font-size: 2.5rem;
    color: ${colorCodes.cardBg3};
  }
  .caret:hover {
    color: ${colorCodes.lightGold};
  }
  .cons_details {
    border-top: 3px solid ${colorCodes.cardBg2};
    border-bottom: 3px solid ${colorCodes.cardBg2};
  }
`;

export const StyledSlideShow = styled.div`
  position: absolute;
  top: 0;
  left: ${(props) => `-${props.leftDist * 3.5}rem`};
  display: flex;
  transition: left 100ms linear;
`;

export function SlideShow(props) {
  const { index, images } = props;
  return (
    <div
      className={
        (props.forTl ? "pt-1 pb-2" : "pt-2 pb-3") + " flex-center relative"
      }
    >
      {props.typeCpn}
      <div className="relative">
        <Box
          className="overflow-hidden relative"
          width="3.5rem"
          height="3.5rem"
        >
          <StyledSlideShow leftDist={index}>
            {images.map((img, i) => (
              <Img
                key={i}
                width="3.5rem"
                src={wikia + img + ".png"}
                alt=""
                draggable={false}
              />
            ))}
          </StyledSlideShow>
        </Box>
        {index > 0 && <Caret dir="left" onClick={props.back} />}
        {index < images.length - 1 && (
          <Caret dir="right" onClick={props.next} />
        )}
      </div>
    </div>
  );
}

const Caret = ({ dir, onClick }) => (
  <p
    className={
      (dir === "right" ? "pl-3 pr-2 right" : "pl-2 pr-3 left") +
      " caret pointer"
    }
    onClick={onClick}
  >
    <i className={"fas fa-caret-" + dir} />
  </p>
);

export const LvBtn = styled(({ dir, ...others }) => (
  <div {...others}>
    <i className={"fas fa-caret-" + dir} />
  </div>
))`
  display: flex;
  padding: 0 0.5rem;
  border-radius: 0.25rem;
  border-width: 3px;
  border-style: solid;
  border-color: ${colorCodes.cardBg3};
  color: ${colorCodes.cardBg3};
  font-size: 1.375rem;
  cursor: pointer;
  :hover {
    border-color: ${colorCodes.green};
    color: ${colorCodes.green};
  }
`;
