import { LGreen, Pyro } from "../../../../styledCpns/DataDisplay";
import {
  BowNaDesc,
  BowPaDesc,
  lightPAs,
  bowCAs,
  TalentLvMults
} from "../../config";

const actvTalents = [
  {
    type: "Normal Attack",
    name: "Firework Flare-Up",
    image: "d/d8/Talent_Firework_Flare-Up",
    desc: [
      { heading: "NA", content: BowNaDesc(5) },
      {
        heading: "CA",
        content: (
          <>
            Performs a more precise Aimed Shot with increased DMG.
            <br />
            While aiming, flames will accumulate on the arrowhead before being
            fired off as an attack. Has different effects based on how long the
            energy has been charged:
            <br /> Charge Level 1: Fires off a flaming arrow that deals{" "}
            <Pyro>Pyro DMG</Pyro>.
            <br /> Charge Level 2: Generates a maximum of 3 Kindling Arrows
            based on time spent charging, releasing them as part of this Aimed
            Shot. Kindling Arrows will home in on nearby opponents, dealing{" "}
            <Pyro>Pyro DMG</Pyro> on hit.
          </>
        )
      },
      { heading: "PA", content: BowPaDesc }
    ],
    stats: [
      {
        name: "1-Hit (x2)",
        dmgTypes: ["NA", "Physical"],
        baseMult: 35.64,
        multType: 4
      },
      {
        name: "2-Hit",
        dmgTypes: ["NA", "Physical"],
        baseMult: 68.38,
        multType: 4
      },
      {
        name: "3-Hit",
        dmgTypes: ["NA", "Physical"],
        baseMult: 88.89,
        multType: 4
      },
      {
        name: "4-Hit (x2)",
        dmgTypes: ["NA", "Physical"],
        baseMult: 46.42,
        multType: 4
      },
      {
        name: "5-Hit",
        dmgTypes: ["NA", "Physical"],
        baseMult: 105.86,
        multType: 4
      },
      ...bowCAs,
      {
        name: "Kindling Arrow",
        dmgTypes: ["CA", "Elemental"],
        baseMult: 16.4,
        multType: 2
      },
      ...lightPAs("bow")
    ]
  },
  {
    type: "Elemental Skill",
    name: "Niwabi Fire-Dance",
    image: "0/03/Talent_Niwabi_Fire-Dance",
    desc: [
      {
        content: (
          <>
            Yoimiya waves a sparkler and causes a ring of saltpeter to surround
            her.
          </>
        )
      },
      {
        heading: "Niwabi Enshou",
        get content() {
          return (
            <>
              {this.buff} and converted to <Pyro>Pyro DMG</Pyro>. During this
              time, Normal Attack: Firework Flare-Up will not generate Kindling
              Arrows at Charge Level 2.
            </>
          );
        },
        buff: (
          <>
            During this time, arrows fired by Yoimiya's{" "}
            <LGreen>Normal Attack</LGreen> will be Blazing Arrows, and their{" "}
            <LGreen>DMG</LGreen> will be increased
          </>
        )
      },
      {
        content: <>This effect will deactivate when Yoimiya leaves the field.</>
      }
    ],
    getXtraLv: (char) => (char.constellation >= 3 ? 3 : 0),
    stats: [
      {
        name: "Blazing Arrow DMG",
        noCalc: true,
        getValue: (lv) =>
          Math.round(10000 + 3791 * TalentLvMults[5][lv]) / 100 +
          "% Normal Attack DMG"
      }
    ],
    otherStats: () => [
      {
        name: "Duration",
        value: "10s"
      },
      {
        name: "CD",
        value: "18s"
      }
    ]
  },
  {
    type: "Elemental Burst",
    name: "Ryuukin Saxifrage",
    image: "a/a7/Talent_Ryuukin_Saxifrage",
    desc: [
      {
        content: (
          <>
            Yoimiya leaps into the air along with her original creation, the
            "Ryuukin Saxifrage," and fires forth blazing rockets bursting with
            surprises that deal AoE <Pyro>Pyro DMG</Pyro> and mark one of the
            hit opponents with Aurous Blaze.
          </>
        )
      },
      {
        heading: "Aurous Blaze",
        content: (
          <>
            All Normal/Charged/Plunging Attacks, Elemental Skills, and Elemental
            Bursts by any party member other than Yoimiya that hit an opponent
            marked by Aurous Blaze will trigger an explosion, dealing AoE{" "}
            <Pyro>Pyro DMG</Pyro>.
            <br />
            When an opponent affected by Aurous Blaze is defeated before its
            duration expires, the effect will pass on to another nearby
            opponent, who will inherit the remaining duration.
          </>
        )
      },
      {
        content: (
          <>
            One Aurous Blaze explosion can be triggered every 2s. When Yoimiya
            is down, Aurous Blaze effects created through her skills will be
            deactivated.
          </>
        )
      }
    ],
    getXtraLv: (char) => (char.constellation >= 5 ? 3 : 0),
    stats: [
      {
        name: "Skill DMG",
        dmgTypes: ["EB", "Elemental"],
        baseMult: 127.2,
        multType: 2
      },
      {
        name: "Aurous Blaze Explosion",
        dmgTypes: ["EB", "Elemental"],
        baseMult: 122,
        multType: 2
      }
    ],
    otherStats: () => [
      {
        name: "Duration",
        value: "10s"
      },
      {
        name: "CD",
        value: "15s"
      }
    ],
    energyCost: 60
  }
];

export default actvTalents;
