import { Red } from "../../../../styledCpns/DataDisplay";
import data from "./index";

export const buffs = [
  {
    src: "Elemental Skill",
    desc: ({ toSelf, char, inputs, ATTRs, charBCs }) => {
      const { bnValue } = buffs[0];
      let result;
      if (toSelf) {
        const A4on = buffs[1].isGranted(char) && charBCs[1].activated;
        result = bnValue(A4on, ATTRs["Energy Recharge"]);
        if (charBCs[0].activated && A4on) {
          result = Math.round(result * 9.1) / 10;
        }
      } else {
        result = bnValue(...inputs);
      }
      return (
        <>
          {data.actvTalents[1].desc[1].buff} <Red>Total bonus: {result}%</Red>.
        </>
      );
    },
    isGranted: () => true,
    affect: "party",
    labels: ["A4 Passive Talent", "Energy Recharge"],
    inputs: [false, 100],
    inputTypes: ["check", "text"],
    maxs: [null, 999],
    addBnes: ({ ATTRs, char, inputs, toSelf, charBCs }) => {
      const { bnValue } = buffs[0];
      ATTRs["Energy Recharge"] += toSelf
        ? bnValue(
            buffs[1].isGranted(char) && charBCs[1].activated,
            ATTRs["Energy Recharge"]
          )
        : bnValue(...inputs);
    },
    bnValue: (boosted, ER) => 20 + (boosted ? Math.round(ER) / 10 : 0)
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => data.pasvTalents[1].desc,
    isGranted: (char) => char.ascension >= 4,
    affect: "self"
  }
];

export const debuffs = [
  {
    src: "Constellation 2",
    desc: () => data.constellation[1].desc,
    isGranted: (char) => char.constellation >= 2,
    addPntes: ({ rdMult }) => {
      rdMult["Electro RES"] -= 15;
    }
  }
];
