import { Red } from "../../../../styledCpns/DataDisplay";
import data from "./index";

export const buffs = [
  {
    src: "Ascension 1 Passive Talent",
    desc: ({ ATTRs }) => (
      <>
        {data.pasvTalents[0].desc}{" "}
        <Red>
          Total healing: {300 + Math.round(ATTRs["Elemental Mastery"] * 1.2)}.
        </Red>
      </>
    ),
    isGranted: (char) => char.ascension >= 1,
    affect: "self"
  },
  {
    src: "Constellation 2",
    desc: () => data.constellation[1].desc,
    isGranted: (char) => char.constellation >= 2,
    affect: "self",
    selfLabels: ["Time (sec)"],
    inputs: [0],
    inputTypes: ["text"],
    maxs: [10]
  },
  {
    src: "Constellation 6",
    desc: ({ ATTRs }) => {
      const [atkPct, healing] = buffs[2].bnValue(ATTRs);
      return (
        <>
          {data.constellation[5].desc}{" "}
          <Red>
            DMG Bonus (base): {Math.round((ATTRs.ATK * atkPct) / 100)}. Healing
            Bonus: {healing}.
          </Red>
        </>
      );
    },
    isGranted: (char) => char.constellation >= 6,
    affect: "self",
    bnValue: (ATTRs) => {
      const EM = Math.min(ATTRs["Elemental Mastery"], 2000);
      return [EM * 0.2, EM * 3];
    }
  }
];
