import { LGreen, Red } from "../../../../styledCpns/DataDisplay";
import { TalentLvMults } from "../../config";
import { getXtraTalentLv } from "../../../../calculators/helpers";
import data from "./index";

export const buffs = [
  {
    src: "Elemental Burst",
    desc: ({ toSelf, inputs }) => (
      <>
        The character within its AoE gains an <LGreen>ATK Bonus</LGreen> that is
        based on Bennett's <LGreen>Base ATK</LGreen>.{" "}
        {!toSelf && <Red>ATK Bonus: {buffs[0].bnValue(...inputs)}.</Red>}
      </>
    ),
    isGranted: () => true,
    affect: "party",
    labels: ["Base ATK", "Elemental Burst Level", "Constellation 1"],
    inputs: [0, 1, false],
    inputTypes: ["text", "text", "check"],
    maxs: [9999, 13, null],
    addBnes: ({ ATTRs, inputs, toSelf, char, charBCs, party }) => {
      const { bnValue } = buffs[0];
      ATTRs.ATK += toSelf
        ? bnValue(
            ATTRs["Base ATK"],
            char["Elemental Burst"] +
              getXtraTalentLv(data.actvTalents[2], char, party),
            buffs[1].isGranted(char) && charBCs[1].activated
          )
        : bnValue(...inputs);
    },
    bnValue: (baseATK, level, boosted) =>
      Math.round(
        baseATK * ((56 * TalentLvMults[2][level] + (boosted ? 20 : 0)) / 100)
      )
  },
  {
    src: "Constellation 1",
    desc: () => <>Gains {data.constellation[0].buff}</>,
    isGranted: (char) => char.constellation >= 1,
    affect: "self"
  },
  {
    src: "Constellation 6",
    desc: () => data.constellation[5].desc,
    isGranted: (char) => char.constellation >= 6,
    affect: "party",
    addBnes: ({ ATTRs }) => {
      ATTRs["Pyro DMG Bonus"] += 15;
    },
    canInfuse: (charData) =>
      ["Sword", "Claymore", "Polearm"].includes(charData.weapon),
    infuseElmt: "Pyro",
    infuseRange: ["NA", "CA", "PA"],
    canBeOverrided: true
  }
];
