import { TalentLvMults } from "../../config";
import { getXtraTalentLv } from "../../../../calculators/helpers";
import data from "./index";

export const buffs = [
  {
    src: "Elemental Burst",
    desc: () => (
      <>
        {data.actvTalents[2].desc[0].lines[0]}
        {data.actvTalents[2].desc[0].lines[1]}
      </>
    ),
    isGranted: () => true,
    affect: "self",
    addFinalBnes: ({ ATTRs, char, charBCs, party }) => {
      const level =
        char["Elemental Burst"] +
        getXtraTalentLv(data.actvTalents[2], char, party);
      let mult = 40 * TalentLvMults[2][level];
      if (buffs[2].isGranted(char) && charBCs[2].activated) {
        mult += 50;
      }
      ATTRs.ATK += Math.round((ATTRs.DEF * mult) / 100);
    },
    canInfuse: () => true,
    infuseRange: ["NA", "CA", "PA"],
    canBeOverrided: false
  },
  {
    src: "Constellation 2",
    desc: () => <>Increases {data.constellation[1].buff}</>,
    isGranted: (char) => char.constellation >= 2,
    affect: "self",
    addBnes: ({ bonuses }) => {
      bonuses.CA.DMG += 15;
    }
  },
  {
    src: "Constellation 6",
    desc: () => data.constellation[5].buff,
    isGranted: (char) => char.constellation >= 6,
    affect: "self"
  }
];
