import { Green } from "../../../../styledCpns/DataDisplay";
import data from "./index";

export const buffs = [
  {
    src: "Elemental Burst",
    desc: () => data.actvTalents[2].desc[0].buff,
    isGranted: () => true,
    affect: "self",
    canInfuse: () => true,
    infuseRange: ["NA", "CA", "PA"],
    canBeOverrided: true
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => (
      <>{data.pasvTalents[1].buff} the Pyro Enchantment provided by Dawn.</>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["Pyro DMG Bonus"] += 20;
    }
  },
  {
    src: "Constellation 1",
    desc: () => data.constellation[0].desc,
    isGranted: (char) => char.constellation >= 1,
    affect: "self",
    addBnes: ({ bonuses }) => {
      bonuses.AllDmg += 15;
    }
  },
  {
    src: "Constellation 2",
    desc: () => (
      <>
        {data.constellation[1].buff} for 10s, up to <Green>3</Green> times.
      </>
    ),
    isGranted: (char) => char.constellation >= 2,
    affect: "self",
    selfLabels: ["Stacks"],
    inputs: [1],
    inputTypes: ["select"],
    maxs: [3],
    addBnes: ({ ATTRs, inputs }) => {
      ATTRs["ATK%"] += 10 * inputs[0];
      ATTRs["Normal ATK SPD"] += 5 * inputs[0];
      ATTRs["Charged ATK SPD"] += 5 * inputs[0];
    }
  },
  {
    src: "Constellation 4",
    desc: () => data.constellation[3].buff,
    isGranted: (char) => char.constellation >= 4,
    affect: "self",
    addBnes: ({ bonuses }) => {
      bonuses.ES.DMG += 40;
    }
  },
  {
    src: "Constellation 6",
    desc: () => data.constellation[5].buff,
    isGranted: (char) => char.constellation >= 6,
    affect: "self",
    addBnes: ({ ATTRs, bonuses }) => {
      bonuses.NA.DMG += 30;
      ATTRs["Normal ATK SPD"] += 30;
    }
  }
];
