import data from "./index";

export const buffs = [
  {
    src: "Ascension 1 Passive Talent",
    desc: () => data.pasvTalents[0].desc,
    isGranted: (char) => char.ascension >= 1,
    affect: "self"
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => data.pasvTalents[1].desc,
    isGranted: (char) => char.ascension >= 4,
    affect: "party",
    addBnes: ({ ATTRs }) => {
      ATTRs["Cryo DMG Bonus"] += 20;
    }
  },
  {
    src: "Constellation 4",
    desc: () => data.constellation[3].buff,
    isGranted: (char) => char.constellation >= 4,
    affect: "party",
    selfLabels: ["Stacks"],
    labels: ["Stacks"],
    inputs: [1],
    inputTypes: ["select"],
    maxs: [5],
    addBnes: ({ bonuses, inputs }) => {
      bonuses.AllDmg += 5 * inputs[0];
    }
  }
];

export const debuffs = [
  {
    src: "Constellation 1",
    desc: () => data.constellation[0].debuff,
    isGranted: (char) => char.constellation >= 1,
    addPntes: ({ rdMult }) => {
      rdMult["Cryo RES"] -= 15;
    }
  }
];
