import { useState, useEffect } from "react";
import { StyledList } from "../styles";
import Overview from "./Overview";
import Details from "./Details";
import characters from "../../../data/characters";

export default function TalentList(props) {
  const [index, setIndex] = useState(-1);
  const [atDetails, setAtDetails] = useState(false);
  const { name } = props.char;
  const charData = characters[name];

  useEffect(() => {
    setAtDetails(false);
  }, [name]);

  return (
    <StyledList className="full-w full-h overflow-hidden relative">
      <Overview
        {...props}
        charData={charData}
        className={"overview " + (atDetails ? "close" : "open")}
        toDetails={(i) => {
          setAtDetails(true);
          setIndex(i);
        }}
      />
      <Details
        charData={charData}
        className={"details " + (atDetails ? "open" : "close")}
        index={index}
        setIndex={setIndex}
        close={() => {
          setAtDetails(false);
          setTimeout(() => setIndex(-1), 200);
        }}
      />
    </StyledList>
  );
}
