import data from "./index";

export const buffs = [
  {
    src: "Ascension 1 Passive Talent",
    desc: () => <>{data.pasvTalents[0].buff}.</>,
    isGranted: (char) => char.ascension >= 1,
    affect: "self",
    addBnes: ({ bonuses }) => {
      bonuses.EB.CR += 0.1;
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => data.pasvTalents[1].desc,
    affect: "self",
    isGranted: (char) => char.ascension >= 4,
    addBnes: ({ ATTRs }) => {
      ATTRs["ATK%"] += 15;
    }
  },
  {
    src: "Constellation 2",
    desc: () => data.constellation[1].buff,
    affect: "self",
    isGranted: (char) => char.constellation >= 2,
    addBnes: ({ bonuses }) => {
      bonuses.ES.DMG += 200;
    }
  },
  {
    src: "Constellation 6",
    desc: () => data.constellation[5].desc,
    affect: "party",
    isGranted: (char) => char.constellation >= 6,
    addBnes: ({ ATTRs }) => {
      ATTRs["ATK%"] += 15;
    }
  }
];
