import data from "./index";

export const buffs = [
  {
    src: "Ascension 1 Passive Talent",
    desc: () => data.pasvTalents[0].desc,
    isGranted: (char) => char.ascension >= 1,
    affect: "self",
    canInfuse: () => true,
    infuseRange: ["NA", "CA", "PA"],
    canBeOverrided: true
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => data.pasvTalents[1].desc,
    isGranted: (char) => char.ascension >= 4,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["CRIT Rate"] += 15;
      ATTRs["Energy Recharge"] += 15;
    }
  },
  {
    src: "Constellation 4",
    desc: () => data.constellation[3].desc,
    isGranted: (char) => char.constellation >= 4,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["ATK%"] += 25;
    }
  },
  {
    src: "Constellation 6",
    desc: () => data.constellation[5].desc,
    isGranted: (char) => char.constellation >= 6,
    affect: "self",
    selfLabels: ["Stacks"],
    inputs: [1],
    inputTypes: ["select"],
    maxs: [4],
    addBnes: ({ ATTRs, inputs }) => {
      ATTRs["Electro DMG Bonus"] += 6 * inputs[0];
    }
  }
];
