import { Anemo } from "../../../../styledCpns/DataDisplay";
import { SwordDesc, TravelerNCPAs } from "../../config";

const actvTalents = [
  {
    type: "Normal Attack",
    name: "Foreign Ironwind",
    image: "1/1f/Talent_Foreign_Ironwind",
    desc: SwordDesc,
    stats: TravelerNCPAs
  },
  {
    type: "Elemental Skill",
    name: "Palm Vortex",
    image: "0/07/Talent_Palm_Vortex",
    desc: [
      {
        content: (
          <>
            Grasping the wind's might, you form a vortex of vacuum in your palm,
            causing continuous <Anemo>Anemo DMG</Anemo> to opponents in front of
            you.
            <br />
            The vacuum vortex explodes when the skill duration ends, causing a
            greater amount of <Anemo>Anemo DMG</Anemo> over a larger area.
          </>
        )
      },
      {
        heading: "Hold",
        content: "DMG and AoE will gradually increase."
      },
      {
        heading: "Elemental Absorption",
        content: "the vortex"
      }
    ],
    getXtraLv: (char) => (char.constellation >= 5 ? 3 : 0),
    stats: [
      {
        name: "Initial Cutting",
        dmgTypes: ["ES", "Elemental"],
        baseMult: 12,
        multType: 2
      },
      {
        name: "Max Cutting",
        dmgTypes: ["ES", "Elemental"],
        baseMult: 16.8,
        multType: 2
      },
      {
        name: "Initial Storm",
        dmgTypes: ["ES", "Elemental"],
        baseMult: 176,
        multType: 2
      },
      {
        name: "Max Storm",
        dmgTypes: ["ES", "Elemental"],
        baseMult: 192,
        multType: 2
      }
    ],
    otherStats: () => [
      {
        name: "Base CD",
        value: "5s"
      },
      {
        name: "Max Charging CD",
        value: "8s"
      }
    ]
  },
  {
    type: "Elemental Burst",
    name: "Gust Surge",
    image: "9/98/Talent_Gust_Surge",
    desc: [
      {
        content: (
          <>
            Guiding the path of the wind currents, you summon a forward-moving
            tornado that pulls objects and opponents towards itself, dealing
            continuous <Anemo>Anemo DMG</Anemo>.
          </>
        )
      },
      {
        heading: "Elemental Absorption",
        content: "the tornado"
      }
    ],
    getXtraLv: (char) => (char.constellation >= 3 ? 3 : 0),
    stats: [
      {
        name: "Tornado DMG",
        dmgTypes: ["EB", "Elemental"],
        baseMult: 80.8,
        multType: 2
      },
      {
        name: "Additional Elemental DMG",
        dmgTypes: ["EB", "Various"],
        baseMult: 24.8,
        multType: 2
      }
    ],
    otherStats: () => [
      {
        name: "Duration",
        value: "6s"
      },
      {
        name: "CD",
        value: "15"
      }
    ],
    energyCost: 60
  }
];

export default actvTalents;
