import { Red } from "../../../../styledCpns/DataDisplay";
import data from "./index";

export const buffs = [
  {
    src: "Ascension 4 Passive Talent",
    desc: () => data.pasvTalents[1].desc,
    isGranted: (char) => char.ascension >= 1,
    affect: "party",
    addBnes: ({ ATTRs }) => {
      ATTRs["Physical DMG Bonus"] += 15;
    }
  },
  {
    src: "Constellation 1",
    desc: () => data.constellation[0].buff,
    isGranted: (char) => char.constellation >= 1,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["Normal ATK SPD"] += 12;
      ATTRs["Charged ATK SPD"] += 12;
    }
  },
  {
    src: "Constellation 2",
    desc: () => <>{data.constellation[1].buff}.</>,
    isGranted: (char) => char.constellation >= 2,
    affect: "self"
  },
  {
    src: "Constellation 6",
    desc: ({ ATTRs }) => (
      <>
        {data.constellation[5].buff}{" "}
        <Red>DMG Bonus (base): {buffs[3].bnValue(ATTRs)}.</Red>
      </>
    ),
    isGranted: (char) => char.constellation >= 6,
    affect: "self",
    addFinalBnes: ({ ATTRs, bonuses }) => {
      bonuses.CA.Flat += buffs[3].bnValue(ATTRs);
    },
    bnValue: (ATTRs) => Math.round(ATTRs.DEF / 2)
  }
];

export const debuffs = [
  {
    src: "Constellation 4",
    desc: () => data.constellation[3].desc,
    isGranted: (char) => char.constellation >= 4,
    addPntes: ({ rdMult }) => {
      rdMult["Physical RES"] -= 15;
    }
  }
];
