import { LGreen, Green, Pyro } from "../../../../styledCpns/DataDisplay";
import { TalentLvMults } from "../../config";
import { getXtraTalentLv } from "../../../../calculators/helpers";
import data from "./index";

export const buffs = [
  {
    src: "Elemental Skill",
    desc: ({ char, party }) => (
      <>
        {data.actvTalents[1].desc[1].buff} by{" "}
        <Green>{buffs[0].bnValue(char, party)}%</Green> and{" "}
        <LGreen>converted</LGreen> to <Pyro>Pyro DMG</Pyro>.
      </>
    ),
    isGranted: () => true,
    affect: "self",
    addBnes: ({ bonuses, char, party }) => {
      bonuses.NA.mult += buffs[0].bnValue(char, party);
    },
    bnValue: (char, party) => {
      const level =
        char["Elemental Skill"] +
        getXtraTalentLv(data.actvTalents[1], char, party);
      return Math.round(37.91 * TalentLvMults[5][level] * 100) / 100;
    },
    canInfuse: () => true,
    infuseRange: ["NA"],
    canBeOverrided: false
  },
  {
    src: "Ascension 1 Passive Talent",
    desc: () => data.pasvTalents[0].desc,
    isGranted: (char) => char.ascension >= 1,
    affect: "self",
    selfLabels: ["Stacks"],
    inputs: [1],
    inputTypes: ["select"],
    maxs: [10],
    addBnes: ({ ATTRs, inputs }) => {
      ATTRs["Pyro DMG Bonus"] += 2 * inputs[0];
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => data.pasvTalents[1].desc,
    isGranted: (char) => char.ascension >= 4,
    affect: "teammates",
    labels: ["Stacks"],
    inputs: [1],
    inputTypes: ["select"],
    maxs: [10],
    addBnes: ({ ATTRs, inputs }) => {
      ATTRs["ATK%"] += 10 + inputs[0];
    }
  },
  {
    src: "Constellation 1",
    desc: () => <>When {data.constellation[0].buff}</>,
    isGranted: (char) => char.constellation >= 1,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["ATK%"] += 20;
    }
  },
  {
    src: "Constellation 2",
    desc: () => data.constellation[1].buff,
    isGranted: (char) => char.constellation >= 2,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["Pyro DMG Bonus"] += 25;
    }
  }
];
