import { Green, Red } from "../../../../styledCpns/DataDisplay";
import { TalentLvMults } from "../../config";
import { incElementalBnes } from "../../../../helpers/calculators";
import { getXtraTalentLv } from "../../../../calculators/helpers";
import data from "./index";

export const buffs = [
  {
    src: "Elemental Skill",
    desc: ({ toSelf, inputs }) => (
      <>
        {data.actvTalents[1].desc[3].content}{" "}
        {!toSelf && <Red>DMG Bonus (base): {buffs[0].bnValue(...inputs)}.</Red>}
      </>
    ),
    isGranted: () => true,
    affect: "party",
    labels: ["Current ATK", "Elemental Skill Level"],
    inputs: [0, 1],
    inputTypes: ["text", "text"],
    maxs: [9999, 13],
    addFinalBnes: ({
      toSelf,
      char,
      ATTRs,
      bonuses,
      inputs,
      infusion,
      party
    }) => {
      const { bnValue } = buffs[0];
      const value = toSelf
        ? bnValue(
            ATTRs.ATK,
            char["Elemental Skill"] +
              getXtraTalentLv(data.actvTalents[1], char, party)
          )
        : bnValue(...inputs);
      incElementalBnes(char.name, infusion, "Cryo", bonuses, "Flat", value);
    },
    bnValue: (ATK, level) =>
      Math.round((ATK * 45.66 * TalentLvMults[2][level]) / 100)
  },
  {
    src: "Ascension 1 Passive Talent",
    desc: () => data.pasvTalents[0].desc,
    isGranted: (char) => char.ascension >= 1,
    affect: "party",
    addBnes: ({ ATTRs }) => {
      ATTRs["Cryo DMG Bonus"] += 15;
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: ({ inputs }) => (
      <>
        {data.pasvTalents[1].lines[0]}
        <span className={inputs[0] ? "" : "unavailable"}>
          {data.pasvTalents[1].lines[1]}
        </span>
        <span className={inputs[1] ? "" : "unavailable"}>
          {data.pasvTalents[1].lines[2]}
        </span>
      </>
    ),
    isGranted: (char) => char.ascension >= 4,
    affect: "party",
    selfLabels: ["Press", "Hold"],
    labels: ["Press", "Hold"],
    inputs: [true, false],
    inputTypes: ["check", "check"],
    addBnes: ({ bonuses, inputs }) => {
      if (inputs[0]) {
        bonuses.ES.DMG += 15;
        bonuses.EB.DMG += 15;
      }
      if (inputs[1]) {
        bonuses.NA.DMG += 15;
        bonuses.CA.DMG += 15;
        bonuses.PA.DMG += 15;
      }
    }
  },
  {
    src: "Constellation 2",
    desc: () => data.constellation[1].desc,
    isGranted: (char) => char.constellation >= 2,
    affect: "party",
    addBnes: ({ char, bonuses, infusion }) => {
      incElementalBnes(char.name, infusion, "Cryo", bonuses, "CD", 0.15);
    }
  },
  {
    src: "Constellation 4",
    desc: () => data.constellation[3].desc,
    isGranted: (char) => char.constellation >= 4,
    affect: "self",
    selfLabels: ["Stacks"],
    inputs: [0],
    inputTypes: ["text"],
    maxs: [50],
    addBnes: ({ bonuses, inputs }) => {
      bonuses.ES.DMG += 5 * inputs[0];
    }
  }
];

export const debuffs = [
  {
    src: "Elemental Burst",
    desc: ({ fromSelf, char, inputs, party }) => (
      <>
        {data.actvTalents[2].desc[0].debuff} by{" "}
        <Green>{debuffs[0].pntValue(fromSelf, char, inputs, party)}%</Green>.
      </>
    ),
    isGranted: () => true,
    labels: ["Elemental Burst Level"],
    inputTypes: ["text"],
    addPntes: ({ rdMult, fromSelf, char, inputs, party }) => {
      const value = debuffs[0].pntValue(fromSelf, char, inputs, party);
      rdMult["Physical RES"] -= value;
      rdMult["Cryo RES"] -= value;
    },
    pntValue: (fromSelf, char, inputs, party) => {
      let value = 0;
      if (fromSelf) {
        value =
          5 +
          char["Elemental Burst"] +
          getXtraTalentLv(data.actvTalents[2], char, party);
      } else if (inputs[0]) {
        value = 5 + inputs[0];
      }
      return Math.min(value, 15);
    }
  }
];
