import { LGreen, Green } from "../../../../styledCpns/DataDisplay";
import { getXtraTalentLv } from "../../../../calculators/helpers";
import data from "./index";

export const buffs = [
  {
    src: "Elemental Burst",
    desc: ({ char, party }) => (
      <>
        Raises Razor's <LGreen>ATK SPD</LGreen> by{" "}
        <Green>{buffs[0].bnValue(char, party)}%</Green>.
      </>
    ),
    isGranted: () => true,
    affect: "self",
    addBnes: ({ ATTRs, char, party }) => {
      ATTRs["Normal ATK SPD"] += buffs[0].bnValue(char, party);
    },
    bnValue: (char, party) => {
      const level =
        char["Elemental Burst"] +
        getXtraTalentLv(data.actvTalents[2], char, party);
      return buffs[0].getPct(level);
    },
    getPct: (lv) => Math.min(24 + lv * 2 - Math.max(lv - 6, 0), 40)
  },
  {
    src: "Constellation 1",
    desc: () => data.constellation[0].desc,
    isGranted: (char) => char.constellation >= 1,
    affect: "self",
    addBnes: ({ bonuses }) => {
      bonuses.AllDmg += 10;
    }
  },
  {
    src: "Constellation 2",
    desc: () => data.constellation[1].desc,
    isGranted: (char) => char.constellation >= 2,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["CRIT Rate"] += 10;
    }
  }
];

export const debuffs = [
  {
    src: "Constellation 4",
    desc: () => data.constellation[3].desc,
    isGranted: (char) => char.constellation >= 4,
    addPntes: ({ rdMult }) => {
      rdMult.DEF += 15;
    }
  }
];
