import data from "./index";

export const buffs = [
  {
    src: "Ascension 1 Passive Talent",
    desc: () => data.pasvTalents[0].buff,
    isGranted: (char) => char.ascension >= 1,
    affect: "self",
    addBnes: ({ bonuses }) => {
      bonuses.CA.DMG += 50;
    }
  },
  {
    src: "Constellation 6",
    desc: () => data.constellation[5].buff,
    isGranted: (char) => char.constellation >= 6,
    affect: "party",
    addBnes: ({ ATTRs }) => {
      ATTRs["Pyro DMG Bonus"] += 10;
    }
  }
];

export const debuffs = [
  {
    src: "Constellation 2",
    desc: () => data.constellation[1].desc,
    isGranted: (char) => char.constellation >= 2,
    addPntes: ({ rdMult }) => {
      rdMult.DEF += 23;
    }
  }
];
