import { TalentLvMults } from "../../config";
import { getXtraTalentLv } from "../../../../calculators/helpers";
import data from "./index";

export const buffs = [
  {
    src: "Elemental Burst",
    desc: () => (
      <>
        {data.actvTalents[2].desc[0].lines[0]}
        {data.actvTalents[2].desc[0].lines[1]}
      </>
    ),
    isGranted: () => true,
    affect: "self",
    addFinalBnes: ({ ATTRs, char, party }) => {
      const level =
        char["Elemental Burst"] +
        getXtraTalentLv(data.actvTalents[2], char, party);
      const mult = 57.6 * TalentLvMults[2][level];
      ATTRs.ATK += Math.round((ATTRs.DEF * mult) / 100);
      ATTRs["Normal ATK SPD"] += 10;
    },
    canInfuse: () => true,
    infuseRange: ["NA", "CA", "PA"],
    canBeOverrided: false
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => data.pasvTalents[1].desc,
    isGranted: (char) => char.ascension >= 4,
    affect: "self"
  },
  {
    src: "Constellation 4",
    desc: () => data.constellation[3].desc,
    isGranted: (char) => char.constellation >= 4,
    affect: "party",
    addBnes: ({ ATTRs }) => {
      ATTRs["DEF%"] += 20;
      ATTRs["ATK%"] += 20;
    }
  },
  {
    src: "Constellation 6",
    desc: () => data.constellation[5].desc,
    isGranted: (char) => char.constellation >= 6,
    affect: "self",
    addBnes: ({ bonuses }) => {
      bonuses.CA.CD += 0.7;
    }
  }
];
