import { useState, useRef } from "react";
import {
  Text,
  Hydro,
  Pyro,
  Cryo,
  Electro
} from "../../../styledCpns/DataDisplay";
import { Select } from "../../../styledCpns/Inputs";
import { Box, AbsoluteBox } from "../../../styledCpns/Layout";
import { Table } from "../StatsTable";
import { LvBtn } from "../styles";
import { TalentLvMults } from "../../../data/characters/config";

const destruct = {
  NA: "Normal Attack",
  CA: "Charged Attack",
  PA: "Plunging Attack"
};

export function TalentInfo({ desc, isActv }) {
  if (!desc || !isActv) {
    return <Text className="mt-2 pt-1">{desc || "To be added..."}</Text>;
  }
  return (
    <div className="flex-col">
      {desc.map(({ heading, content }, i) => (
        <div key={i} className="mt-2">
          {heading ? (
            <Text variant="h6" color="lightGold">
              {destruct[heading] ? destruct[heading] : heading}
            </Text>
          ) : null}
          <Text className={heading ? "" : "mt-1"}>
            {heading === "Elemental Absorption" ? (
              <>
                If {content} comes into contact with <Hydro>Hydro</Hydro>/
                <Pyro>Pyro</Pyro>/<Cryo>Cryo</Cryo>/<Electro>Electro</Electro>,
                it will deal additional <b>Elemental DMG</b> of that type.
                <br />
                Elemental Absorption may only occur once per use.
              </>
            ) : (
              content
            )}
          </Text>
        </div>
      ))}
    </div>
  );
}

const LeftCol = ({ children }) => (
  <Text className="pr-5" variant="subtitle1" color="#f0e873">
    {children}
  </Text>
);

const RightCol = ({ children }) => (
  <Text variant="subtitle1" align="right">
    {children}
  </Text>
);

export function SkillAttributes({ tlData, isActv, code }) {
  const [lv, setLv] = useState(1);
  const ref = useRef();

  const adjustLv = (up) => {
    const adjust = () =>
      setLv((prev) => (up ? Math.min(prev + 1, 15) : Math.max(prev - 1, 1)));
    adjust();
    ref.current = setInterval(adjust, 200);
  };
  if (!tlData) return null;
  const { stats, otherStats, energyCost } = tlData;
  const needSelect = isActv && tlData.type !== "Alternate Sprint";

  return (
    <div className="mt-2 pt-1">
      <Box
        className={(needSelect ? "pl-3" : "pr-3") + " pb-2 flex-center"}
        bgColor="cardBg1"
        style={{ position: "sticky", top: 0 }}
      >
        {needSelect && (
          <AbsoluteBox top={0} left="2.5rem">
            <LvBtn
              dir="down"
              onMouseDown={() => adjustLv(false)}
              onMouseUp={() => clearInterval(ref.current)}
              onMouseLeave={() => clearInterval(ref.current)}
            />
          </AbsoluteBox>
        )}
        {needSelect && (
          <AbsoluteBox top={0} right="2.5rem">
            <LvBtn
              dir="up"
              onMouseDown={() => adjustLv(true)}
              onMouseUp={() => clearInterval(ref.current)}
              onMouseLeave={() => clearInterval(ref.current)}
            />
          </AbsoluteBox>
        )}
        <Text className="mr-1" variant="h6">
          Lv.
        </Text>
        {needSelect ? (
          <Select
            className="pr-2"
            fontSize="1.125rem"
            fontWeight={700}
            color="white"
            value={lv}
            onChange={(e) => setLv(+e.target.value)}
          >
            {[...Array(15).keys()].map((_, i) => (
              <option key={i}>{i + 1}</option>
            ))}
          </Select>
        ) : (
          <Text className="px-1" variant="h6" fontWeight={700}>
            1
          </Text>
        )}
      </Box>
      <Table>
        {stats
          .filter((stat) => !stat.conditional)
          .map((stat, i) => (
            <div key={i} className="pb-1 row">
              <LeftCol>{stat.name}</LeftCol>
              <RightCol>
                {stat.noCalc ? (
                  <b>{stat.getValue(lv)}</b>
                ) : (
                  <Value stat={stat} lv={lv} />
                )}
              </RightCol>
            </div>
          ))}
        {otherStats
          ? otherStats(lv).map((stat, j) => (
              <div key={"o" + j} className="pb-1 row">
                <LeftCol>{stat.name}</LeftCol>
                <RightCol>{stat.value}</RightCol>
              </div>
            ))
          : null}
        {energyCost ? (
          <div className="pb-1 row">
            <LeftCol>Energy Cost</LeftCol>
            <RightCol>{energyCost}</RightCol>
          </div>
        ) : null}
      </Table>
    </div>
  );
}

function Value({ stat, lv }) {
  const { baseSType, baseMult, multType, baseFlat } = stat;
  return (
    <b>
      {Array.isArray(baseMult)
        ? baseMult
            .map((mult) => getValue(mult, multType, lv, true, baseSType))
            .join("+")
        : baseMult || stat.lockedMult
        ? getValue(baseMult || stat.lockedMult, multType, lv, true, baseSType)
        : null}
      {baseMult && baseFlat ? " + " : ""}
      {baseFlat ? getValue(baseFlat, stat.flatType, lv, false) : null}
    </b>
  );
}

function getValue(base, type, lv, isPct, baseSType) {
  let result = base * TalentLvMults[type][lv];
  if (isPct) {
    result = Math.round(result * 100) / 100;
    result += "%";
  } else {
    result = Math.round(result);
  }
  if (baseSType)
    result += " " + (baseSType === "HP" ? "Max " + baseSType : baseSType);
  return result;
}
