import { Red } from "../../../../styledCpns/DataDisplay";
import { TalentLvMults } from "../../config";
import { getXtraTalentLv } from "../../../../calculators/helpers";
import data from "./index";

export const buffs = [
  {
    src: "Elemental Burst",
    desc: ({ char, party, ATTRs, charBCs }) => {
      const { NA, CA, ES } = buffs[0].bnValue(char, party, ATTRs, charBCs);
      return (
        <>
          {data.actvTalents[2].desc[1].buff}{" "}
          <Red>
            Normal Attack DMG Bonus (base): {NA}. Charged Attack DMG Bonus
            (base): {CA}. Bake-Kurage DMG Bonus (base): {ES}.
          </Red>
        </>
      );
    },
    isGranted: () => true,
    affect: "self",
    addFinalBnes: ({ ATTRs, bonuses, char, charBCs, party }) => {
      const { NA, CA, ES } = buffs[0].bnValue(char, party, ATTRs, charBCs);
      bonuses.NA.Flat += NA;
      bonuses.CA.Flat += CA;
      bonuses.ES.Flat += ES;
    },
    bnValue: (char, party, ATTRs, charBCs) => {
      const level =
        char["Elemental Burst"] +
        getXtraTalentLv(data.actvTalents[2], char, party);
      const buffMults = {
        NA: 4.84,
        CA: 6.78,
        ES: 7.1
      };
      const result = {};
      for (let type of ["NA", "CA", "ES"]) {
        let mult = buffMults[type] * TalentLvMults[2][level];
        if (type !== "ES" && buffs[1].isGranted(char) && charBCs[1].activated) {
          mult += ATTRs["Healing Bonus"] * 0.15;
        }
        result[type] = Math.round((ATTRs.HP * mult) / 100);
      }
      return result;
    }
  },
  {
    src: "Ascension 4 Passive Talent",
    desc: () => data.pasvTalents[1].desc,
    isGranted: (char) => char.ascension >= 4,
    affect: "self"
  },
  {
    src: "Constellation 4",
    desc: () => <>{data.constellation[3].buff}.</>,
    isGranted: (char) => char.constellation >= 4,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["Normal ATK SPD"] += 10;
    }
  },
  {
    src: "Constellation 6",
    desc: () => data.constellation[5].desc,
    isGranted: (char) => char.constellation >= 6,
    affect: "self",
    addBnes: ({ ATTRs }) => {
      ATTRs["Hydro DMG Bonus"] += 40;
    }
  }
];
